@import url('https://fonts.googleapis.com/css2?family=Chelsea+Market&display=swap');
@import 'normalize';
@import 'variables';

*, 
*:before, 
*:after {
  box-sizing: border-box;
}

body{
	color: $theme-black;
	font-family: $font-primary;
	line-height: 1.3;
	&.no-scroll {
		height: 100vh;
  	overflow-y: hidden;
	}
}
a {
	color: $theme-black;
	text-decoration: none;
	cursor: pointer;
}
button {
	cursor: pointer;
}
img {
	max-width: 100%;
}
button:focus {
	outline: none;
}
.zoom{
	cursor: pointer;
	display: block;
  transition: all 0.2s ease;
}
.zoom:hover{
  transform: scale(1.1); 
}
/* ----------- loader ----------- */
.loader-overlay {
  width: 100vw;
  height: 100vh;
  background: url('../img/loader.gif') center no-repeat #fbfbfb;
  background-size: 10.5rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  overflow: hidden;
}

/* ----------- navbar ----------- */
.navbar {
	background-color: $theme-white;
	position: sticky;
	top: 0;
	left: 0;
	z-index: 10;
	.container {
		display: none;
		max-width: 80rem;
		margin: 0 auto;
		padding: .5rem 1.5rem;
		font-size: 1.25rem;
		&.open {
			max-width: none;
			width: 100vw;
			height: 100vh;
			padding: 0;
			position: fixed;
			left: 0;
			top: 0;
			z-index: 10;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: #6682bb;
			animation-duration: .6s;
			.site-brand {
				display: none;
			}
			a {
				font-size: 2rem;
				color: $theme-white;
				padding: .5rem 0;
			}
		}
	}
	.site-brand {
		padding: 0 1rem;
	}
	.link-list {
		padding: 0;
		margin: 0;
		list-style: none;
		display: flex;
		flex-direction: column;
		align-items: center;
		a {
			display: block;
			padding: 0 1rem;
		}
	}
	li{
		padding: .5rem 0;
		&.active {
			background-image: url('../img/white-line.png');
			background-size: contain;
			background-repeat: no-repeat;
			background-position: bottom;
			@include breakpoint(md) {
				background-image: url('../img/line.png');
			}
		}
	}
	@include breakpoint(md) {
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		.link-list {
			flex-direction: row;
		}
		li.active {
			background-size: 5.5rem .675rem;
		}
	}
}
.hamburger-icon {
	font-size: 1.5rem;
	text-align: center;
	color: $theme-blue;
	position: fixed;
	z-index: 11;
	top: 1rem;
	right: 1.5rem;
	width: 3rem;
	height: 3rem;
	background-color: $theme-white;
	border-radius: 2rem;
	box-shadow: 0px 0px 6px 4px rgba(50,50,50,.08);
	border: none;
	padding: .75rem;
	i {
		display: block;
	}
	@include breakpoint(md) {
		display: none;
	}
}
/* ----------- home ----------- */
.home {
	width: 100%;
	height: 100vh;
  min-height: 35rem;
  overflow-x: hidden;
  background-image: url("../img/home-bg-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  position: relative;
  .intro-container {
  	height: 100%;
  	display: flex;
  	align-items: center;
  	justify-content: center;
  	align-items: center;
  }
  #intro-text {
  	font-size: 1.5rem;
  	text-align: center;
  	max-width: 14rem;
  	margin-bottom: 40%;
  }
  .helicopter-container {
  	animation-duration: 20s;
	  animation-iteration-count: infinite;
	  img {
	  	max-width: 8rem;
	  	position: absolute;
	  	top: 4.5rem;
	  	left: -7rem;
	  	z-index: 1;
	  	overflow: hidden;
	  }
  }
  @include breakpoint(sm) {
  	min-height: 40rem;
  	background-size: 90%;
		background-image: url("../img/home-bg.jpg");
		#intro-text {
			font-size: 2rem;
			max-width: 25rem;
			margin-bottom: 20%;
		}
		.helicopter-container {
			animation-duration: 26s;
	  	img {
	  		max-width: 9rem;
	  	}
	  }
  }
  @include breakpoint(md) {
  	#intro-text {
  		font-size: 2.25rem;
	  	max-width: 25rem;
	  }
	  .helicopter-container {
	  	img {
	  		max-width: 11rem;
	  		top: 1rem;
	  		left: -9rem;
	  	}
	  }
  }
}
/* ----------- about ----------- */
#about {
  position: relative;
  height: 100vh;
  min-height: 31.25rem;
  h2 {
  	font-size: 2rem;
  	text-align: center;
  	position: absolute;
  	top: 5%;
  	left: 0;
  	width: 100%;
  }
  .back-btn{
	  width: 6rem;
	  position: absolute;
	  bottom: 0;
		z-index: 1;
	}
	.contact-arrow{
		white-space: nowrap;
		i{
			transition: all 0.2s ease-in-out;
			&:hover {
				margin-left: .75rem;
			}
		}
	}
  @include breakpoint(sm) {
  	min-height: 44rem;
  	.back-btn{
	  	width: 7.5rem;
  	}
  }
  @include breakpoint(md) {
	  h2 {
	  	font-size: 2.25rem;
	  }
  }
  @include breakpoint(lg) {
 	  h2 {
	 		font-size: 2.75vw;
		}
	}

  .word-bubble {
  	display: flex;
  	align-items: center;
  	background-repeat: no-repeat;
	  background-position: center;
	  background-size: 100% auto;
  	width: 40%;

  	p {
	  	font-size: 3vw;
	  	padding: 0 .7rem;
	  }
	  .more-link {
	  	color: $theme-blue;
	  	text-decoration: underline;
	  	:hover {
	  		text-decoration: none;
	  	}
	  }
	  @include breakpoint(iphone5) {
	  	p {
		  	font-size: .7rem;
		  	padding: 0 1rem;
		  }
		  br {
		  	display: none;
		  }
	  }
	  @include breakpoint(sm) {
	  	width: 35%;
	  	p {
		  	font-size: 1.25rem;
		  	padding: 0 1.25rem;
		  }
	  }
	  @include breakpoint(lg) {
	  	p {
		  	font-size: 1.75vw;
		  	padding: 0 1.5rem;
		  }
	  }
  }
  .left-bubble {
  	height: 88%;
  	justify-content: flex-start;
  	background-image: url('../img/left-bubble.svg');
  	@include breakpoint(sm) {
  		height: 85%;
  	}
  }
  .right-bubble {
  	height: 95%;
  	justify-content: center;
  	background-image: url('../img/right-bubble.svg');
  	@include breakpoint(sm) {
  		height: 100%;
  	}
  }
  #aboutMe{
	  width: 100%;
	  height: 100vh;
	  min-height: 31.25rem;
	  position: absolute;
	  top: 0;
	  left: 0;
	  background-image: url('../img/about-bg-mobile.jpg');
	  background-repeat: no-repeat;
	  background-position: center;
	  background-size: 100% auto;
	  background-color: $theme-white;
	  .container {
	  	height: 100%;
	  	padding: 0 .25rem;
	  	display: flex;
	  	flex-direction: row;
	  	justify-content: space-between;
	  	align-items: flex-end;
	  }
	  @include breakpoint(sm) {
		  min-height: 44rem;
	  	background-size: auto 100%;
	  	background-image: url('../img/about-bg.jpg');
	  	.container {
		  	padding: 0 2%;
		  	align-items: flex-start;
		  }
	  }
	  @include breakpoint(md) {
	  	.container {
		  	padding: 0 8%;
		  }
		}
	  @include breakpoint(lg) {
	  	.container {
		  	padding: 0 15%;
		  }
	  }
	}
	/* ----------- about coder----------- */
	#aboutCoding{
	  width: 100%;
	  height: 100vh;
	  min-height: 41.5rem;
	  display: flex;
	  align-items: center;
	  justify-content: center;
	  .container {
	  	display: flex;
	  	flex-direction: column;
	  	align-items: center;
	  	max-width: 90rem;
	  	@include breakpoint(sm) {
	  		flex-direction: row;
	  	}
	  }
	  .coder-intro {
	  	padding: 0 0 3rem;
	  	max-width: 90%;
	  	position: relative;
	  	.code-lines {
	  		display: block;
	  		padding-left: 2rem;
	  	}
	  	p {
	  		font-size: .875rem;
	  		line-height: 1.5;
	  	}
	  	@include breakpoint(sm) {
	  		width: 65%;
	  		padding: 0 2rem 4rem;
	  		max-width: none;
	  		.code-lines {
		  		display: block;
		  		padding-left: 2.75rem;
		  	}
	  		p {
	  			font-size: 1.15rem;
	  			padding: 1.5rem 7% 3.5rem 13%;
	  			background-image: url('../img/word-bubble.png');
				  background-repeat: no-repeat;
				  background-position: center;
				  background-size: 100% 100%;
	  		}
	  	}
			@include breakpoint(md) {
				width: 60%;
				#left-back {
					left: 8%;
				}
				p {
					padding: 3rem 7% 5rem 13%;
					font-size: 1.25rem;
				}
			}
			@include breakpoint(lg) {
				p {
					padding: 5rem 15%;
					font-size: 1.5rem;
				}
			}
	  }
	  .side-img {
	  	order: -1;
	  	max-width: 80%;
	  	text-align: center;
	  	img {
	  		max-width: 25rem;
	  		width: 90%;
	  	}
	  	@include breakpoint(sm) {
	  		order: 0;
	  		width: 35%;
	  		max-width: none;
	  		img {
		  		max-width: 100%;
		  	}
	  	}
	  	@include breakpoint(md) {
	  		width: 40%;
	  	}
	  }
	}
	/* ----------- about Min----------- */
	#aboutMin {
		width: 100%;
	  height: auto;
	  min-height: 41.5rem;
	  display: flex;
	  align-items: center;
	  justify-content: center;
		.container {
			max-width: 90rem;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}
		.self-intro {
			padding-bottom: 3rem;
			position: relative;
		}
		#right-back {
			right: 1.5rem;
		}
		.side-img {
			img {
				object-fit: cover;
    		object-position: top;
			}
		}
		@include breakpoint(sm) {
			height: 100vh;
			.container {
		 		flex-direction: row;
			}
			.side-img {
				width: 50%;
				padding: 0 2rem;
			}
			.self-intro {
				width: 50%;
			}
		}
		@include breakpoint(md) {
			.intro-content {
				width: 90%;
				max-width: 40rem;
			}
			.side-img {
				width: 35%;
			}
			.self-intro {
				width: 65%;
				padding-bottom: 5rem;
			}
			#right-back {
				right: 12%;
			}
		}
		.intro-content {
			margin: 0 auto;
			padding: 0 1.25rem;
			p {
		 		line-height: 1.35;
		 		margin: 1rem 0;
		 		font-size: .875rem;
		 	}
		 	@include breakpoint(sm) {
		 		p {
		 			font-size: 1.15rem;
		 		}
		 	}
		 	@include breakpoint(md) {
		 		width: 85%;
		 		background-image: url('../img/word-bubble-1.png');
				background-repeat: no-repeat;
			  background-position: center;
				background-size: 100% 100%;
		 		padding: 1.5rem 10% 4rem 5%;
		 	}
		 	@include breakpoint(lg) {
				max-width: 50rem;
		 		p {
		 			font-size: 1.5rem;
		 		}
		 	}
		}
	}
}
/* ----------- portfolio ----------- */
#portfolio {
	padding: 1rem;
	width: 100%;
	height: 100vh;
	min-height: 48rem;
	.container {
		height: 100%;
		max-width: 30rem;
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	.card-container {
		padding: 0 1rem;
		display: flex;
		justify-content: center;
		align-items: center;
		& + * {
			margin-top: 2rem;
		}
	}
	.card {
		max-width: 22.5rem;
		padding: 1rem 0;
    text-align: center;
    border-radius: 1.5rem;
    background-color: $theme-white;
    box-shadow: 0 0 1rem 0 rgba(100,100,100,0.6);
    img {
    	max-width: 80%;
    }
	}
	h2 {
		font-size: 1rem;
	}
	@include breakpoint(sm) {
		padding: 3rem 1rem;
		.container {
			max-width: 44rem;
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 3rem; 
		}
		.card-container {
			padding: 0 10%;
			background-image: url('../img/portfolio-bg.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: 100% auto;
			& + * {
				margin-top: 0;
			}
		}
		.card {
			max-width: none;
			padding: 2rem 0;
		}
		h2 {
			font-size: 1.25rem;
			min-height: 3.25rem;
			padding: 0 2rem;
		}
	}
	@include breakpoint(md) {
		.container {
			max-width: 50rem;
			grid-gap: 3rem; 
		}
		.card-container {
			padding: 0 15%;
		}
		h2 {
			font-size: 1.75rem;
			min-height: 4.5rem;
		}
	}
	@include breakpoint(lg) {
		.container {
			max-width: 70rem;
			grid-gap: 5rem;
		}
	}
}


/* ----------- contact ----------- */
#contact {
	width: 100%;
	height: 100vh;
	min-height: 48rem;
	// background-image: url('../img/contact-bg.png');
	background-image: url('../img/test-1.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: auto 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 2rem;
	@include breakpoint(sm) {
		background-size: 100vw 100%;
	}
}
#phone{
	position: relative;
	max-width: 21rem;
	background-color: $theme-white;
	padding: 4.5rem 1rem 5rem;
	border-radius: 2.5rem;
	box-shadow: 0 0 1rem 0 rgba(100,100,100,0.6);
	h2 {
		font-size: 2rem;
		color: $theme-white;
		text-align: center;
		margin: 0 0 1.5rem;
		@include breakpoint(sm) {
			font-size: 2.75rem;
		}
	}
	&:before {
		content: '';
		width: 5rem;
		height: .5rem;
		border-radius: .25rem;
		background-color: #d6d6d6;
		position: absolute;
		left: calc(50% - 2.5rem);
		top: 2.25rem;
		z-index: 1;
	}
	&:after {
		content: '';
		width: 3.25rem;
		height: 3.25rem;
		background-color: #d6d6d6;
		border-radius: 50%;
		position: absolute;
		left: calc(50% - 1.625rem);
		bottom: .75rem;
		z-index: 1;
	}
}
.phone-content {
	padding: 2rem 1rem 6rem;
	background-color: $theme-blue;
	@include breakpoint(sm) {
		padding: 3rem 1.5rem 7rem;
	}
}
.social-media{
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	grid-gap: 1.5rem; 
	background-color: $theme-white;
	border-radius: 2rem;
	padding: 1.25rem;
	@include breakpoint(sm) {
		padding: 1.5rem;
	}
  a {
  	display: block;
  	max-width: 4.75rem;
  	img {
  		border-radius: 1rem;
  		box-shadow: 0 0 .75rem 0 rgba(100,100,100,.3);
  	}
  	@include breakpoint(sm) {
  		max-width: 6.75rem;
  	}
  }
}
/* ----------- contact modal ----------- */
.contact-modal{
  z-index: 99;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  padding: 5rem 0 2.5rem;
  height: auto;
  position: fixed;
  background: $theme-white;
  display: none;
  overflow-y: scroll;
  .container {
  	padding: 0 2rem;
  	height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  h2 {
  	font-size: 2rem;
  	text-align: center;
  	margin: 0;
  }
 	.modal-content{
		width: 18.75rem;
		min-height: 40rem;
	}
  @include breakpoint(sm) {
  	padding: 20vh 0 0;
  	h2 {
	  	font-size: 2.5rem;
	  }
  	.modal-content{
			width: 40rem;
			min-height: 43.75rem;
		}
  }
}
#close-modal{
  position: absolute;
  z-index: 100;
  top: 1.5rem;
  right: 1.5rem;
}
.contact-form{
	margin-top: 3rem;
	label{
		display: block;
	  font-size: 1.25rem;
	  margin-bottom: .5rem;
	}
	input, 
	textarea{
		width: 100%;
	  border: .125rem solid $theme-black;
	  border-radius: .75rem;
	  font-size: 1rem;
	  padding: .75rem 1rem;
	  &:focus {
	  	border: .125rem solid #b7b7b7;
		  outline: none;
		  box-shadow: none;
	  }
	}
	.form-group {
		margin-bottom: 1.5rem;
	}
}
.submit-button{
  margin: 5% auto 0;
  width: 100%;
  background-color:$theme-black;
  color: $theme-white;
  font-size: 1.25rem;
  border: .125rem solid $theme-black;
  border-radius: .75rem;
  padding: .75rem;
  display: block;
  @include breakpoint(sm) {
  	max-width:18rem
  }
  &:hover{
	  background-color: $theme-white;
	  color: $theme-black;
	}
	&:focus {
		outline: none;
	}
}

